'use client'

import { ReactNode, useEffect, useState } from 'react'
import Link from 'next/link'
import { useRouter, usePathname } from 'next/navigation'
import { supabase } from '@/lib/supabaseClient'
import { User } from '@supabase/supabase-js'
import './globals.css'

export default function RootLayout({ children }: { children: ReactNode }) {
  const [user, setUser] = useState<User | null>(null)
  const [loading, setLoading] = useState(true)
  const router = useRouter()
  const pathname = usePathname()

  useEffect(() => {
    // Get initial session
    const getSession = async () => {
      const { data: { session } } = await supabase.auth.getSession()
      setUser(session?.user ?? null)
      setLoading(false)
    }

    getSession()

    // Listen for auth changes
    const { data: { subscription } } = supabase.auth.onAuthStateChange(
      async (event, session) => {
        setUser(session?.user ?? null)
        setLoading(false)
      }
    )

    return () => subscription.unsubscribe()
  }, [])

  const handleSignOut = async () => {
    await supabase.auth.signOut()
    router.push('/auth/login')
  }

  const isAuthPage = pathname?.startsWith('/auth')

  return (
    <html lang="en">
      <head>
        <title>SocialNet - Share Your Moments</title>
        <meta name="description" content="A modern social networking platform" />
        <meta name="viewport" content="width=device-width, initial-scale=1" />
      </head>
      <body className="min-h-screen bg-background text-foreground">
        {!isAuthPage && (
          <header className="sticky top-0 z-50 bg-card/95 backdrop-blur supports-[backdrop-filter]:bg-card/60 border-b border-border">
            <nav className="container mx-auto px-4 py-3">
              <div className="flex items-center justify-between">
                <Link 
                  href="/" 
                  className="text-xl font-bold text-foreground hover:text-primary transition-colors"
                >
                  SocialNet
                </Link>
                
                <div className="flex items-center space-x-6">
                  <Link 
                    href="/" 
                    className={`text-sm font-medium transition-colors hover:text-primary ${
                      pathname === '/' ? 'text-primary' : 'text-muted-foreground'
                    }`}
                  >
                    Home
                  </Link>
                  
                  {user ? (
                    <>
                      <Link 
                        href="/upload" 
                        className={`text-sm font-medium transition-colors hover:text-primary ${
                          pathname === '/upload' ? 'text-primary' : 'text-muted-foreground'
                        }`}
                      >
                        Upload
                      </Link>
                      <Link 
                        href="/profile" 
                        className={`text-sm font-medium transition-colors hover:text-primary ${
                          pathname === '/profile' ? 'text-primary' : 'text-muted-foreground'
                        }`}
                      >
                        Profile
                      </Link>
                      <button 
                        onClick={handleSignOut}
                        className="text-sm font-medium text-muted-foreground hover:text-primary transition-colors"
                      >
                        Sign Out
                      </button>
                    </>
                  ) : (
                    <>
                      <Link 
                        href="/auth/login" 
                        className="text-sm font-medium text-muted-foreground hover:text-primary transition-colors"
                      >
                        Login
                      </Link>
                      <Link 
                        href="/auth/signup" 
                        className="text-sm font-medium bg-primary text-primary-foreground px-3 py-1.5 rounded-md hover:bg-primary/90 transition-colors"
                      >
                        Sign Up
                      </Link>
                    </>
                  )}
                </div>
              </div>
            </nav>
          </header>
        )}

        <main className="flex-1">
          {loading ? (
            <div className="flex items-center justify-center min-h-[50vh]">
              <div className="text-center">
                <div className="animate-spin rounded-full h-8 w-8 border-b-2 border-primary mx-auto mb-4"></div>
                <p className="text-muted-foreground">Loading...</p>
              </div>
            </div>
          ) : (
            children
          )}
        </main>

        {/* Mobile bottom navigation */}
        {!isAuthPage && user && (
          <nav className="md:hidden fixed bottom-0 left-0 right-0 bg-card border-t border-border">
            <div className="flex items-center justify-around py-2">
              <Link 
                href="/" 
                className={`flex flex-col items-center py-2 px-3 text-xs ${
                  pathname === '/' ? 'text-primary' : 'text-muted-foreground'
                }`}
              >
                <div className="w-6 h-6 mb-1 flex items-center justify-center">
                  <div className="w-4 h-4 border-2 border-current rounded"></div>
                </div>
                Home
              </Link>
              <Link 
                href="/upload" 
                className={`flex flex-col items-center py-2 px-3 text-xs ${
                  pathname === '/upload' ? 'text-primary' : 'text-muted-foreground'
                }`}
              >
                <div className="w-6 h-6 mb-1 flex items-center justify-center">
                  <div className="w-4 h-4 border-2 border-current rounded flex items-center justify-center">
                    <div className="w-1 h-1 bg-current rounded-full"></div>
                  </div>
                </div>
                Upload
              </Link>
              <Link 
                href="/profile" 
                className={`flex flex-col items-center py-2 px-3 text-xs ${
                  pathname === '/profile' ? 'text-primary' : 'text-muted-foreground'
                }`}
              >
                <div className="w-6 h-6 mb-1 flex items-center justify-center">
                  <div className="w-4 h-4 bg-current rounded-full"></div>
                </div>
                Profile
              </Link>
            </div>
          </nav>
        )}
        
        {/* Add bottom padding for mobile navigation */}
        {!isAuthPage && user && <div className="md:hidden h-16"></div>}
      </body>
    </html>
  )
}
